import Stats from 'stats.js';
import { initCanvas, Shader } from '../../lib/shader.js';
import { initGUI } from './gui.js';

function main() {
    // Initialize core ThreeJS components
    const canvas = document.getElementById('canvas');

    // Get function to call in animation frame
    const { render, newShader } = initCanvas(canvas);

    const config = initGUI(newShader);

    // Show statistics like FPS
    const stats = new Stats();
    stats.showPanel(0); // Show FPS
    document.body.appendChild(stats.dom);

    // Render loop
    const onAnimationFrameHandler = (timeStamp) => {
        const time = timeStamp * 0.001; // convert to seconds

        stats.begin();
        render(time, config.cameraAngles, config.cameraPos, config.shader);
        stats.end();

        window.requestAnimationFrame(onAnimationFrameHandler);
    };
    window.requestAnimationFrame(onAnimationFrameHandler);

    // Resize Handler
    const windowResizeHandler = () => {
        // Lookup the size the browser is displaying the canvas.
        const displayWidth = canvas.clientWidth;
        const displayHeight = canvas.clientHeight;

        // Check if the canvas is not the same size.
        if (canvas.width !== displayWidth || canvas.height !== displayHeight) {
            // Make the canvas the same size
            canvas.width = displayWidth;
            canvas.height = displayHeight;
        }
        const { innerHeight, innerWidth } = window;
    };
    windowResizeHandler();
    window.addEventListener('resize', windowResizeHandler, false);
}

window.onload = main;
