export function buildSDF(ops, p) {
    if (ops.length === 0) return `MAX_DISTANCE + 1.0`;

    const stack = [];

    ops.forEach((op) => {
        switch (op.type) {
            case 'translate':
            case 'scale':
            case 'infiniteTile':
            case 'finiteTile':
            case 'rotateX':
            case 'rotateY':
            case 'rotateZ':
            case 'bend':
            case 'twist':
            case 'modPolar':
            case 'elongate':
                const shape = stack.pop();
                shape.transformations.push(op);
                stack.push(shape);
                break;
            case 'sub':
            case 'union':
            case 'intersect':
            case 'smoothUnion':
            case 'smoothSub':
            case 'smoothIntersect':
            case 'unionStairs':
            case 'intersectionStairs':
            case 'differenceStairs':
                const shape1 = stack.pop();
                const shape2 = stack.pop();
                op.transformations = [];
                op.shapes = [shape1, shape2];
                stack.push(op);
                break;
            case 'noise':
            case 'onion':
                const tshape = stack.pop();
                op.transformations = [];
                op.shapes = [tshape];
                stack.push(op);
                break;
            default:
                op.transformations = [];
                stack.push(op);
        }
    });

    return toGLSL(stack[0], p);
}

function toGLSL(t, p) {
    switch (t.type) {
        case 'noise':
            return `opNoise(${transformGLSL(t.transformations, p)}, ${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${t.noiseProportion.toFixed(2)})`;
        case 'onion':
            return `opOnion(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${t.thickness.toFixed(2)})`;
        case 'union':
            return `min(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(t.shapes[1], transformGLSL(t.transformations, p))})`;
        case 'sub':
            return `max(-${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(t.shapes[1], transformGLSL(t.transformations, p))})`;
        case 'intersect':
            return `max(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(t.shapes[1], transformGLSL(t.transformations, p))})`;
        case 'smoothUnion':
            return `opSmoothU(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(
                t.shapes[1],
                transformGLSL(t.transformations, p)
            )}, ${t.k.toFixed(2)})`;
        case 'smoothSub':
            return `opSmoothSub(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(
                t.shapes[1],
                transformGLSL(t.transformations, p)
            )}, ${t.k.toFixed(2)})`;
        case 'smoothIntersect':
            return `opSmoothIntersect(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(
                t.shapes[1],
                transformGLSL(t.transformations, p)
            )}, ${t.k.toFixed(2)})`;
        case 'unionStairs':
            return `opUnionStairs(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(
                t.shapes[1],
                transformGLSL(t.transformations, p)
            )}, ${t.r.toFixed(2)}, ${t.n.toFixed(2)})`;
        case 'intersectionStairs':
            return `opIntersectionStairs(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(
                t.shapes[1],
                transformGLSL(t.transformations, p)
            )}, ${t.r.toFixed(2)}, ${t.n.toFixed(2)})`;
        case 'differenceStairs':
            return `opDifferenceStairs(${toGLSL(
                t.shapes[0],
                transformGLSL(t.transformations, p)
            )}, ${toGLSL(
                t.shapes[1],
                transformGLSL(t.transformations, p)
            )}, ${t.r.toFixed(2)}, ${t.n.toFixed(2)})`;
        case 'sphere':
            return `sdSphere(${transformGLSL(
                t.transformations,
                p
            )}, ${t.radius.toFixed(2)})`;
        case 'box':
            return `sdBox(${transformGLSL(t.transformations, p)}, vec3(${
                t.corner.x
            }, ${t.corner.y}, ${t.corner.z}))`;
        case 'roundBox':
            return `sdRoundBox(${transformGLSL(t.transformations, p)}, vec3(${
                t.corner.x
            }, ${t.corner.y}, ${t.corner.z}), ${t.r.toFixed(2)})`;
        case 'cylinder':
            return `sdCappedCylinder(${transformGLSL(
                t.transformations,
                p
            )}, ${t.height.toFixed(2)}, ${t.radius.toFixed(2)})`;
        case 'roundCylinder':
            return `sdRoundedCylinder(${transformGLSL(
                t.transformations,
                p
            )}, ${t.rA.toFixed(2)}, ${t.rB.toFixed(2)}, ${t.h.toFixed(2)})`;
        case 'plane':
            return `sdPlane(${transformGLSL(t.transformations, p)}, vec4(${
                t.normal.x
            }, ${t.normal.y}, ${t.normal.z}, ${t.normal.w}))`;
        case 'torus':
            return `sdTorus(${transformGLSL(t.transformations, p)}, vec2(${
                t.t.x
            }, ${t.t.y}))`;
        case 'elongatedTorus':
            return `sdElongatedTorus(${transformGLSL(
                t.transformations,
                p
            )}, vec3(${t.h.x}, ${t.h.y}, ${t.h.z}), vec2(${t.torus.tx}, ${
                t.torus.ty
            }))`;
        case 'link':
            return `sdLink(${transformGLSL(
                t.transformations,
                p
            )}, ${t.length.toFixed(2)}, ${t.r1.toFixed(2)}, ${t.r2.toFixed(
                2
            )})`;
        case 'ellipsoid':
            return `sdEllipsoid(${transformGLSL(t.transformations, p)}, vec3(${
                t.r.x
            }, ${t.r.y}, ${t.r.z}))`;
        case 'cone':
            return `sdCone(${transformGLSL(t.transformations, p)}, vec2(${
                t.c.x
            }, ${t.c.y}), ${t.h.toFixed(2)})`;
        case 'pyramid':
            return `sdPyramid(${transformGLSL(
                t.transformations,
                p
            )}, ${t.h.toFixed(2)})`;
    }
}

function transformGLSL(transformations, p) {
    transformations.reverse();
    return transformations.reduce((s, t) => {
        switch (t.type) {
            case 'translate':
                return `opTx(${s}, vec3(${t.offset.x}, ${t.offset.y}, ${t.offset.z}))`;
            case 'scale':
                return `opScale(${s}, ${t.factor.toFixed(2)})`;
            case 'infiniteTile':
                return `opInf(${s}, vec3(${t.cell.x}, ${t.cell.y}, ${t.cell.z}))`;
            case 'finiteTile':
                return `opFiniteRep(${s}, vec3(${t.cell.x}, ${t.cell.y}, ${t.cell.z}), vec3(${t.l.x}, ${t.l.y}, ${t.l.z}))`;
            case 'rotateX':
                return `opRotX(${s}, ${t.a.toFixed(2)})`;
            case 'rotateY':
                return `opRotY(${s}, ${t.a.toFixed(2)})`;
            case 'rotateZ':
                return `opRotZ(${s}, ${t.a.toFixed(2)})`;
            case 'bend':
                return `opBend(${s}, ${t.k.toFixed(2)})`;
            case 'twist':
                return `opTwist(${s}, ${t.k.toFixed(2)})`;
            case 'modPolar':
                return `opModPolar(${s}, ${t.reps.toFixed(2)})`;
            case "elongate":
                // this is def not right
                return `opElongate(${s}, vec3(${t.h.x}, ${t.h.y}, ${t.h.z})).w + ${toGLSL(t.shapes[0], transformGLSL(t.transformations, p))}` 
        }
    }, p);
}
