import dat from 'dat.gui';
import { Shader } from '../../lib/shader.js';
import { buildSDF } from '../../lib/generator.js';
import libSrc from '../../../shaders/lib.frag';
import engineSrc from '../../../shaders/engine.frag';

class Config {
    #maxSteps = 100;
    #maxDistance = 1;
    #maxMarchDistance = 150;
    #cameraAngles = { x: 0.5, y: 0.1, z: 0, xSpeed: 0, ySpeed: 0, zSpeed: 0 };
    #cameraPos = { x: 0, y: 0, z: 0 };
    #backgroundColor = [0, 0, 0, 1];
    #hist;
    #ops = [];
    #folders = [];
    #shader;
    #newShader;

    constructor(newShader, hist) {
        this.#newShader = newShader;
        this.#hist = hist;
    }

    #invalidate() {
        this.#shader = null;
    }

    get maxSteps() {
        return this.#maxSteps;
    }

    set maxSteps(v) {
        this.#maxSteps = v;
        this.#invalidate();
    }

    get maxDistance() {
        return this.#maxDistance;
    }

    set maxDistance(v) {
        this.#maxDistance = v;
        this.#invalidate();
    }

    get maxMarchDistance() {
        return this.#maxMarchDistance;
    }

    set maxMarchDistance(v) {
        this.#maxMarchDistance = v;
        this.#invalidate();
    }

    get cameraAngles() {
        return this.#cameraAngles;
    }

    set cameraAngles(v) {
        this.#cameraAngles = v;
        this.#invalidate();
    }

    get cameraPos() {
        return this.#cameraPos;
    }

    set cameraPos(v) {
        this.#cameraPos = v;
        this.#invalidate();
    }

    get backgroundColor() {
        return this.#backgroundColor;
    }

    set backgroundColor(v) {
        this.#backgroundColor = v;
        this.#invalidate();
    }

    pop() {
        this.#hist.removeFolder(this.#folders.pop());
        this.#ops.pop();
        this.#invalidate();
    }

    sphere() {
        const op = { type: `sphere`, radius: 1 };
        const f = this.#hist.addFolder(`${this.#ops.length}: sphere`);
        f.add(op, 'radius', 0, 30, 0.1).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    box() {
        const op = { type: `box`, corner: { x: 1, y: 1, z: 1 } };
        const f = this.#hist.addFolder(`${this.#ops.length}: box`);
        f.add(op.corner, 'x', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.corner, 'y', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.corner, 'z', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    roundBox() {
        const op = { type: `roundBox`, corner: { x: 1, y: 1, z: 1 }, r: 0.25 };
        const f = this.#hist.addFolder(`${this.#ops.length}: roundBox`);
        f.add(op.corner, 'x', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.corner, 'y', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.corner, 'z', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op, 'r', 0, 1, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    cylinder() {
        const op = { type: `cylinder`, height: 1, radius: 1 };
        const f = this.#hist.addFolder(`${this.#ops.length}: cylinder`);
        f.add(op, 'height', 0, 30, 0.1).onChange(() => this.#invalidate());
        f.add(op, 'radius', 0, 30, 0.1).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    roundCylinder() {
        const op = { type: `roundCylinder`, rA: 1, rB: 1, h: 1 };
        const f = this.#hist.addFolder(`${this.#ops.length}: roundCylinder`);
        f.add(op, 'h', 0, 30, 0.1).onChange(() => this.#invalidate());
        f.add(op, 'rA', 0, 10, 0.1).onChange(() => this.#invalidate());
        f.add(op, 'rB', 0, 10, 0.1).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    plane() {
        const op = { type: `plane`, normal: { x: 0, y: 1, z: 0, w: 0 } };
        const f = this.#hist.addFolder(`${this.#ops.length}: plane`);
        f.add(op.normal, 'x', 0, 1, 0.01).onChange(() => this.#invalidate());
        f.add(op.normal, 'y', 0, 1, 0.01).onChange(() => this.#invalidate());
        f.add(op.normal, 'z', 0, 1, 0.01).onChange(() => this.#invalidate());
        f.add(op.normal, 'w', -30, 30, 0.1).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    torus() {
        const op = { type: `torus`, t: { x: 0.25, y: 0.25 } };
        const f = this.#hist.addFolder(`${this.#ops.length}: torus`);
        f.add(op.t, 'x', 0, 20, 0.01).onChange(() => this.#invalidate());
        f.add(op.t, 'y', 0, 20, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    link() {
        const op = { type: `link`, length: 1, r1: 0.25, r2: 0.25 };
        const f = this.#hist.addFolder(`${this.#ops.length}: link`);
        f.add(op, 'length', 0, 20, 0.01).onChange(() => this.#invalidate());
        f.add(op, 'r1', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.add(op, 'r2', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    ellipsoid() {
        const op = { type: `ellipsoid`, r: { x: 0.5, y: 0.5, z: 0.5 } };
        const f = this.#hist.addFolder(`${this.#ops.length}: ellipsoid`);
        f.add(op.r, 'x', 0, 20, 0.01).onChange(() => this.#invalidate());
        f.add(op.r, 'y', 0, 20, 0.01).onChange(() => this.#invalidate());
        f.add(op.r, 'z', 0, 20, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    cone() {
        const op = { type: `cone`, c: { x: 0.5, y: 0.5 }, h: 1 };
        const f = this.#hist.addFolder(`${this.#ops.length}: cone`);
        f.add(op.c, 'x', 0, 1, 0.01).onChange(() => this.#invalidate());
        f.add(op.c, 'y', 0, 1, 0.01).onChange(() => this.#invalidate());
        f.add(op, 'h', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    pyramid() {
        const op = { type: `pyramid`, h: 1 };
        const f = this.#hist.addFolder(`${this.#ops.length}: pyramid`);
        f.add(op, 'h', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    translate() {
        const op = { type: `translate`, offset: { x: 0, y: 0, z: 0 } };
        const f = this.#hist.addFolder(`${this.#ops.length}: translate`);
        f.add(op.offset, 'x', -30, 30, 0.001).onChange(() =>
            this.#invalidate()
        );
        f.add(op.offset, 'y', -30, 30, 0.001).onChange(() =>
            this.#invalidate()
        );
        f.add(op.offset, 'z', -30, 30, 0.001).onChange(() =>
            this.#invalidate()
        );
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    scale() {
        const op = { type: `scale`, factor: 1 };
        const f = this.#hist.addFolder(`${this.#ops.length}: scale`);
        f.add(op, 'factor', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    noise() {
        const op = { type: `noise`, noiseProportion: 0.15 };
        const f = this.#hist.addFolder(`${this.#ops.length}: noise`);
        f.add(op, 'noiseProportion', 0.01, 0.5, 0.01).onChange(() =>
            this.#invalidate()
        );
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    onion() {
        const op = { type: `onion`, thickness: 0.5 };
        const f = this.#hist.addFolder(`${this.#ops.length}: onion`);
        f.add(op, 'thickness', 0, 5, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    union() {
        const op = { type: `union` };
        const f = this.#hist.addFolder(`${this.#ops.length}: union`);
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    sub() {
        const op = { type: `sub` };
        const f = this.#hist.addFolder(`${this.#ops.length}: sub`);
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    intersect() {
        const op = { type: `intersect` };
        const f = this.#hist.addFolder(`${this.#ops.length}: intersect`);
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    smoothUnion() {
        const op = { type: `smoothUnion`, k: 0.8 };
        const f = this.#hist.addFolder(`${this.#ops.length}: smoothUnion`);
        f.add(op, 'k', 0, 2, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    smoothSub() {
        const op = { type: `smoothSub`, k: 0.8 };
        const f = this.#hist.addFolder(`${this.#ops.length}: smoothSub`);
        f.add(op, 'k', 0, 2, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    smoothIntersect() {
        const op = { type: `smoothIntersect`, k: 0.8 };
        const f = this.#hist.addFolder(`${this.#ops.length}: smoothIntersect`);
        f.add(op, 'k', 0, 2, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    unionStairs() {
        const op = { type: `unionStairs`, r: 0.4, n: 4 };
        const f = this.#hist.addFolder(`${this.#ops.length}: unionStairs`);
        f.add(op, 'r', 0, 2, 0.01).onChange(() => this.#invalidate());
        f.add(op, 'n', 0, 10, 1).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    intersectionStairs() {
        const op = { type: `intersectionStairs`, r: 0.4, n: 4 };
        const f = this.#hist.addFolder(
            `${this.#ops.length}: intersectionStairs`
        );
        f.add(op, 'r', 0, 2, 0.01).onChange(() => this.#invalidate());
        f.add(op, 'n', 0, 10, 1).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    differenceStairs() {
        const op = { type: `differenceStairs`, r: 0.4, n: 4 };
        const f = this.#hist.addFolder(`${this.#ops.length}: differenceStairs`);
        f.add(op, 'r', 0, 2, 0.01).onChange(() => this.#invalidate());
        f.add(op, 'n', 0, 10, 1).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    modPolar() {
        const op = { type: `modPolar`, reps: 5 };
        const f = this.#hist.addFolder(`${this.#ops.length}: modPolar`);
        f.add(op, 'reps', 0, 20, 1).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    infiniteTile() {
        const op = { type: `infiniteTile`, cell: { x: 1, y: 1, z: 1 } };
        const f = this.#hist.addFolder(`${this.#ops.length}: infiniteTile`);
        f.add(op.cell, 'x', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.cell, 'y', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.cell, 'z', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    finiteTile() {
        const op = {
            type: `finiteTile`,
            cell: { x: 1, y: 1, z: 1 },
            l: { x: 1, y: 1, z: 1 },
        };
        const f = this.#hist.addFolder(`${this.#ops.length}: finiteTile`);
        f.add(op.cell, 'x', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.cell, 'y', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.cell, 'z', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.l, 'x', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.l, 'y', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.add(op.l, 'z', 0, 30, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    rotateX() {
        const op = { type: `rotateX`, a: 0 };
        const f = this.#hist.addFolder(`${this.#ops.length}: rotateX`);
        f.add(op, 'a', 0, 2 * Math.PI, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    rotateY() {
        const op = { type: `rotateY`, a: 0 };
        const f = this.#hist.addFolder(`${this.#ops.length}: rotateY`);
        f.add(op, 'a', 0, 2 * Math.PI, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    rotateZ() {
        const op = { type: `rotateZ`, a: 0 };
        const f = this.#hist.addFolder(`${this.#ops.length}: rotateZ`);
        f.add(op, 'a', 0, 2 * Math.PI, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    bend() {
        const op = { type: `bend`, k: 0.4 };
        const f = this.#hist.addFolder(`${this.#ops.length}: bend`);
        f.add(op, 'k', 0, 1.0, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    twist() {
        const op = { type: `twist`, k: 0.4 };
        const f = this.#hist.addFolder(`${this.#ops.length}: twist`);
        f.add(op, 'k', 0, 1.0, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    elongatedTorus() {
        const op = {
            type: `elongatedTorus`,
            h: { x: 0.2, y: 0.0, z: 0.2 },
            torus: { tx: 0.25, ty: 0.25 },
        };
        const f = this.#hist.addFolder(`${this.#ops.length}: elongatedTorus`);
        f.add(op.h, 'x', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.add(op.h, 'y', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.add(op.h, 'z', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.add(op.torus, 'tx', 0, 20, 0.01).onChange(() => this.#invalidate());
        f.add(op.torus, 'ty', 0, 20, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
    }

    elongate() {
        const op = { type: `elongate`, h: { x: 0.2, y: 0.0, z: 0.2 } };
        const f = this.#hist.addFolder(`${this.#ops.length}: elongate`);
        f.add(op.h, 'x', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.add(op.h, 'y', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.add(op.h, 'z', 0, 10, 0.01).onChange(() => this.#invalidate());
        f.open();
        this.#ops.push(op);
        this.#folders.push(f);
        this.#invalidate();
      }

    get shader() {
        if (this.#shader) return this.#shader;

        const sdf = buildSDF(this.#ops, 'p');
        const sceneSrc = `
      const int MAX_STEPS = ${this.maxSteps};
      const float MAX_MARCH_DISTANCE = ${this.maxMarchDistance.toFixed(2)};
      const float MAX_DISTANCE = ${this.maxDistance.toFixed(2)};
      const vec4 BACKGROUND_COLOR = vec4(${this.backgroundColor[0] / 256}, ${
            this.backgroundColor[1] / 256
        }, ${this.backgroundColor[2] / 256}, ${this.backgroundColor[3]});

      void init() {}

      float sdf(vec3 p, out Material mat) {
        mat = Material(vec3(0.0,0.4, 0.6), vec3(0.0,0.4, 0.6), vec3(0.0,0.4, 0.6), 10.0, NONREFLECT);
        return ${sdf};
      }
    `;
        const lightsSrc = `
      const int N_LIGHTS = 2;

      Light lights[N_LIGHTS] =
      Light[N_LIGHTS](
          Light(vec3(10,20,20), vec3(1,1,1), 1.0, 1.0),
          Light(vec3(10,20,-20), vec3(1,1,1), 1.0, 1.0));
    `;
        this.#shader = this.#newShader(
            libSrc + lightsSrc + sceneSrc + engineSrc
        );
        return this.#shader;
    }

    save() {
        const blob = new Blob([this.shader.source], { type: 'text/plain' });
        const url = URL.createObjectURL(blob);
        window.open(url);
        URL.revokeObjectURL(url);
    }
}

export function initGUI(newShader) {
    const hist = new dat.GUI();
    const config = new Config(newShader, hist);
    const gui = new dat.GUI();

    const max = gui.addFolder('max');
    max.add(config, 'maxSteps', 0, 400, 1);
    max.add(config, 'maxDistance', 0, 1, 0.01);
    max.add(config, 'maxMarchDistance', 0, 400, 0.1);

    const cameraAngle = gui.addFolder('cameraAngle');
    cameraAngle.add(config.cameraAngles, 'x', 0, 2 * Math.PI);
    cameraAngle.add(config.cameraAngles, 'y', 0, 2 * Math.PI);
    cameraAngle.add(config.cameraAngles, 'z', 0, 2 * Math.PI);
    cameraAngle.add(config.cameraAngles, 'xSpeed', 0, 1);
    cameraAngle.add(config.cameraAngles, 'ySpeed', 0, 1);
    cameraAngle.add(config.cameraAngles, 'zSpeed', 0, 1);

    const cameraPos = gui.addFolder('cameraPos');
    cameraPos.add(config.cameraPos, 'x');
    cameraPos.add(config.cameraPos, 'y');
    cameraPos.add(config.cameraPos, 'z');

    gui.addColor(config, 'backgroundColor');

    gui.add(config, 'save');

    const shapes = gui.addFolder('shapes');
    shapes.add(config, 'sphere');
    shapes.add(config, 'box');
    shapes.add(config, 'roundBox');
    shapes.add(config, 'cylinder');
    shapes.add(config, 'roundCylinder');
    shapes.add(config, 'plane');
    shapes.add(config, 'torus');
    shapes.add(config, 'elongatedTorus');
    shapes.add(config, 'link');
    shapes.add(config, 'ellipsoid');
    shapes.add(config, 'cone');
    shapes.add(config, 'pyramid');

    const operations = gui.addFolder('operations');
    operations.add(config, 'union');
    operations.add(config, 'smoothUnion');
    operations.add(config, 'sub');
    operations.add(config, 'smoothSub');
    operations.add(config, 'intersect');
    operations.add(config, 'smoothIntersect');
    operations.add(config, 'unionStairs');
    operations.add(config, 'intersectionStairs');
    operations.add(config, 'differenceStairs');
    operations.add(config, 'noise');
    operations.add(config, 'onion');

    const transformations = gui.addFolder('transformations');
    transformations.add(config, 'modPolar');
    transformations.add(config, 'translate');
    transformations.add(config, 'scale');
    transformations.add(config, 'infiniteTile');
    transformations.add(config, 'finiteTile');
    transformations.add(config, 'rotateX');
    transformations.add(config, 'rotateY');
    transformations.add(config, 'rotateZ');
    transformations.add(config, 'bend');
    transformations.add(config, 'twist');
    transformations.add(config, 'elongate');

    hist.add(config, 'pop');

    return config;
}
