import { mat4, vec3 } from 'gl-matrix';

const vs = `#version 300 es
    // an attribute is an input (in) to a vertex shader.
    // It will receive data from a buffer
    in vec4 a_position;
    out vec4 v_position;

    // all shaders have a main function
    void main() {

      // gl_Position is a special variable a vertex shader
      // is responsible for setting
      gl_Position = a_position;
      v_position = a_position;
    }
  `;

class Shader {
    #gl;
    #program;
    #vao;
    #uniforms;

    constructor(source, gl) {
        this.source = source;
        this.#gl = gl;
    }

    get program() {
        if (!this.#program) {
            this.#program = initShaderProgram(this.#gl, vs, this.source);
        }
        return this.#program;
    }

    get vao() {
        if (!this.#vao) {
            this.#vao = initVertexArray(
                this.#gl,
                this.#gl.getAttribLocation(this.program, 'a_position')
            );
        }
        return this.#vao;
    }

    get uniforms() {
        if (!this.#uniforms) {
            this.#uniforms = {
                resolution: this.#gl.getUniformLocation(
                    this.program,
                    'iResolution'
                ),
                rot: this.#gl.getUniformLocation(this.program, 'rot'),
                time: this.#gl.getUniformLocation(this.program, 'iTime'),
            };
        }
        return this.#uniforms;
    }
}

// Returns a function that should be called during every animation frame
export function initCanvas(canvas) {
    const gl = canvas.getContext('webgl2');

    if (gl === null) {
        alert(
            'Unable to initialize WebGL. Your browser or machine may not support it.'
        );
        return;
    }

    const newShader = (src) => new Shader(src, gl);

    const render = (time, cameraAngles, cameraPos, shader) => {
        gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);

        // Tell it to use our program (pair of shaders)
        gl.useProgram(shader.program);

        // Bind the attribute/buffer set we want.
        gl.bindVertexArray(shader.vao);

        // Set resolution
        gl.uniform2f(
            shader.uniforms.resolution,
            gl.canvas.width,
            gl.canvas.height
        );

        // Set rotation/translation matrix
        const rot = mat4.create();
        mat4.fromXRotation(rot, cameraAngles.x + cameraAngles.xSpeed * time);
        mat4.rotateY(rot, rot, cameraAngles.y + cameraAngles.ySpeed * time);
        mat4.rotateZ(rot, rot, cameraAngles.z + cameraAngles.zSpeed * time);
        mat4.translate(
            rot,
            rot,
            vec3.fromValues(cameraPos.x, cameraPos.y, cameraPos.z)
        );
        gl.uniformMatrix4fv(shader.uniforms.rot, false, rot);

        // Set time
        gl.uniform1f(shader.uniforms.time, time);

        gl.drawArrays(
            gl.TRIANGLES,
            0, // offset
            6 // num vertices to process
        );
    };

    return { render, newShader };
}

function initShaderProgram(gl, vsSource, fsSource) {
    const vertexShader = gl.createShader(gl.VERTEX_SHADER);
    gl.shaderSource(vertexShader, vsSource);

    const fragmentShader = gl.createShader(gl.FRAGMENT_SHADER);
    gl.shaderSource(fragmentShader, fsSource);

    const program = gl.createProgram();
    gl.attachShader(program, vertexShader);
    gl.attachShader(program, fragmentShader);

    gl.compileShader(vertexShader);
    gl.compileShader(fragmentShader);
    gl.linkProgram(program);

    if (!gl.getProgramParameter(program, gl.LINK_STATUS)) {
        console.error('Link failed: ' + gl.getProgramInfoLog(program));
        console.error('vs info-log: ' + gl.getShaderInfoLog(vertexShader));
        console.error('fs info-log: ' + gl.getShaderInfoLog(fragmentShader));
        return null;
    }

    return program;
}

function initVertexArray(gl, positionAttributeLocation) {
    // Create a vertex array object (attribute state)
    const vao = gl.createVertexArray();

    // and make it the one we're currently working with
    gl.bindVertexArray(vao);

    // Create a buffer to put three 2d clip space points in
    const positionBuffer = gl.createBuffer();

    // Bind it to ARRAY_BUFFER (think of it as ARRAY_BUFFER = positionBuffer)
    gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer);

    // fill it with a 2 triangles that cover clip space
    gl.bufferData(
        gl.ARRAY_BUFFER,
        new Float32Array([
            -1,
            -1, // first triangle
            1,
            -1,
            -1,
            1,
            -1,
            1, // second triangle
            1,
            -1,
            1,
            1,
        ]),
        gl.STATIC_DRAW
    );

    // Turn on the attribute
    gl.enableVertexAttribArray(positionAttributeLocation);

    // Tell the attribute how to get data out of positionBuffer (ARRAY_BUFFER)
    gl.vertexAttribPointer(
        positionAttributeLocation,
        2, // 2 components per iteration
        gl.FLOAT, // the data is 32bit floats
        false, // don't normalize the data
        0, // 0 = move forward size * sizeof(type) each iteration to get the next position
        0 // start at the beginning of the buffer
    );

    return vao;
}
